import { BurgerIcon, ListIcon, ProfileIcon } from "@ya.praktikum/react-developer-burger-ui-components/dist/ui/icons";
import { Logo } from '@ya.praktikum/react-developer-burger-ui-components/dist/ui/logo';
import styles from './app-header.module.css'
import { NavLink, useLocation } from "react-router-dom";

const AppHeader = () => {
  const location = useLocation();
  
  const menuButtons = [
    { text: "", icon: <BurgerIcon type="secondary"/>, link: '/' },
    { text: " ", icon: <ListIcon type="secondary"/>, link: '/feed' },
  ];

  const isActive = (link: string): boolean => location.pathname === link;

  return (
    <header className={styles.header}>
        <div className={styles.menu}>
            {menuButtons.map((btn, index) => {
                return <NavLink to={btn.link} key={index} className={`${styles.button} mt-4 ${isActive(btn.link) && styles.active}`}>{btn.icon} <span className={`text text_type_main-small ml-2`}>{btn.text}</span></NavLink>
            })}
        <NavLink to='/' className={styles.logo}><Logo/></NavLink>
        <NavLink to='/profile' className={`${styles.button} mt-4 ${isActive('/profile') && styles.active}`}><ProfileIcon type="secondary"/> <span className={`text text_type_main-small ml-2`}> </span></NavLink>
        </div>
    </header >
  );
};

export { AppHeader };
